@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body{margin:0;font-family:'Roboto',  sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#F7F7F7}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.max-container{padding-left:15px;padding-right:15px;margin:0 auto}.d-flex{display:flex}

.header{height:72px;background-color:#2F736E}

.FirstCol{flex:1 1}.FirstCol-Cont{max-width:200px;width:100%}.FirstCol-Cont>img{width:100%}

.Input{width:212px;padding:8px 12px;border:1px solid #292929;border-radius:3px;padding-left:12px;padding-right:12px;margin-bottom:24px}

.FormGroup{display:flex;flex-direction:column;margin-right:64px}.FormGroup label{text-align:left;font:500 16px/21px Roboto;color:#292929;margin-bottom:8px}.FormGroup input{width:212px;padding:8px 12px;border:1px solid #292929;border-radius:3px;padding-left:12px;padding-right:12px;margin-bottom:24px}

.Summery{display:flex;margin-top:22px;flex-direction:column;background:#ebebeb 0% 0% no-repeat padding-box}.Summery>h1{text-align:left;font:Bold 20px/26px Roboto;letter-spacing:0px;color:#292929;opacity:1}.Summery-Results{display:flex;padding-bottom:22px}.Summery-Results-1{flex:1 1;display:flex}.Summery-Results-1>div{display:flex;flex-direction:column;align-items:flex-start;margin-left:4px}.Summery-Results-1>div>p{text-align:left;letter-spacing:0px;color:#191919;opacity:1}.Summery-Results-1>div:first-child{margin-left:0px}.Summery-Results-1>div:first-child>p{text-align:left;font:Medium 16px/21px Roboto;letter-spacing:0px;color:#191919}

.Item-1{display:flex;justify-content:space-between}

.Table{display:flex;flex-direction:column;margin-top:36px}.Table-Title{padding:13px 0px;background:#0d8a74 0% 0% no-repeat padding-box}.Table-Title-1{display:flex;justify-content:space-between}.Table-Title-1>p{text-align:left;font:Medium 16px/21px Roboto;letter-spacing:0px;color:#191919 !important;opacity:1}

.Main{flex:3 1;position:relative;padding-bottom:36px}.Main>p{text-align:left;font:Regular 14px/19px Roboto;letter-spacing:0px;color:#292929;opacity:1;font-size:13px}.Main-ShowBtn{display:flex;justify-content:flex-end}.Main-ShowBtn>button{text-align:left;font:Medium 16px/21px Roboto;letter-spacing:0px;color:#ffffff;text-transform:uppercase;opacity:1;background:#2f736e 0% 0% no-repeat padding-box;border-radius:3px;border:none;opacity:1;padding:9px 15px}.Main-Summery{display:flex;flex-direction:column;background:#ebebeb 0% 0% no-repeat padding-box}.Main-Summery>h1{text-align:left;font:Bold 20px/26px Roboto;letter-spacing:0px;color:#292929;opacity:1}.Main-Summery-Results{display:flex}.Main-Summery-Results-1{flex:1 1;display:flex}.Main-Summery-Results-1>div{display:flex;flex-direction:column;align-items:flex-start;margin-left:4px}.Main-Summery-Results-1>div>p{text-align:left;letter-spacing:0px;color:#191919;opacity:1}.Main-Summery-Results-1>div:first-child{margin-left:0px}.Main-Summery-Results-1>div:first-child>p{text-align:left;font:Medium 16px/21px Roboto;letter-spacing:0px;color:#191919}.Main-Icons{position:absolute;background:#ffffff 0% 0% no-repeat padding-box;box-shadow:0px 0px 10px #00000040;border-radius:5px;opacity:1;width:286px;bottom:-65px;left:200px;display:flex;flex-direction:column;padding:11px 25px}.Main-Icons>div{display:flex}.Main-Icons>div>p{text-align:left;font:Regular 16px/21px Roboto;letter-spacing:0px;color:#3d3d3d;opacity:1}.Main-Icons>div>img{margin-left:14px}.Main-Icons>div>img:first-child{margin-left:0px}

.LastCol{flex:1 1}.LastCol-Cont{max-width:200px;width:100%}.LastCol-Cont>img{width:100%}

.MainContent{padding-top:114px;padding-bottom:140px;background-color:#f7f7f7}.MainContent-Cont{display:flex}.MainContent-Cont-Test{max-width:10000px;border:1px solid red}.MainContent .main-col{box-shadow:0 0 20px #00000026;background-color:#fff;margin:0 16px}.MainContent .main-col .main-col--header{padding:36px 72px 8px;background-image:url(/projects/react/calculator/build/static/media/col-header-bg.dc9b3ef9.png);background-repeat:no-repeat}.MainContent .main-col .main-col--header h2{text-align:left;font:Bold 36px/48px Roboto;letter-spacing:0;color:#292929;opacity:1;margin:0}.MainContent .main-col .main-col--header p{margin:0;text-align:left;font:400 14px/19px Roboto;letter-spacing:0;color:#292929;opacity:1;max-width:81%}.MainContent .main-col .form{padding:36px 72px 0}.MainContent .main-col .form .submit{padding:9px 22px;background:#b5dcd5 0 0 no-repeat padding-box;border-radius:3px;text-transform:uppercase;font:500 16px/21px Roboto;color:#292929;border:0;justify-content:center;display:flex;margin-bottom:28px;margin-top:12px}.MainContent .main-col .info-share{padding:0 72px;text-align:left}.MainContent .main-col .info-share p{margin:0;font:400 14px/19px Roboto;color:#292929}


